import { Component, OnInit } from '@angular/core';
import { ISkill } from './iskills';
import { SkillSetService } from './skills.service';
import { ISkillSet } from './iskillset';

@Component({
  selector: 'app-skills',
  templateUrl: './skills.component.html',
  styleUrls: ['./skills.component.css']
})
export class SkillsComponent implements OnInit {

  skills: ISkill[] = [];
  skillSet: ISkillSet[] = [];
  errorMessage: string;
  constructor(private skillService: SkillSetService) {
  }
  ngOnInit(): void {
    this.skillService.getSkills().subscribe({
      next: skills => this.skills = skills,
      error: err => this.errorMessage = err
    });
    console.log("getting data " + this.skills);
  }

}
